@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono-regular-webfont.woff2") format("woff2"), url("../fonts/ptmono-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-bold-webfont.woff2") format("woff2"), url("../fonts/robotocondensed-bold-webfont.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed-regular-webfont.woff2") format("woff2"), url("../fonts/robotocondensed-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Georgia", serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: normal;
  color: #666666;
  background-color: #1b2d37; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

.page-header {
  background-color: #1b2d37;
  background-image: url("../img/bg-header-1.png");
  background-position: bottom center;
  background-repeat: no-repeat; }

.page-header__wrapper {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px; }

.page-header__contacts {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-top: 33px;
  margin-bottom: 231px; }

.page-header__logo {
  margin-right: auto; }

.logo img {
  width: 216px;
  height: 52px; }

.page-header__link {
  font-family: "PT Mono", "Georgia", serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  padding-left: 40px;
  margin-left: 80px; }
  .page-header__link:active {
    opacity: .7; }

.btn {
  display: inline-block;
  padding: 12px 25px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border-radius: 5px;
  overflow: hidden;
  transition: all 0.3s ease-out;
  cursor: pointer; }
  .btn--request-a-call {
    font-size: 14px;
    line-height: 16px;
    margin-left: 50px;
    color: #ffffff;
    background-color: transparent;
    border: 1px solid #ffffff; }
    .btn--request-a-call:hover, .btn--request-a-call:focus {
      background-color: #ffffff;
      color: #1b2d37; }
    .btn--request-a-call:active {
      opacity: 0.7; }
  .btn--consultation {
    font-size: 16px;
    line-height: 19px;
    color: #ffffff;
    background-color: #00b2ff;
    border: none;
    margin-bottom: 275px; }
  .btn--detail {
    margin-bottom: 110px;
    font-size: 14px;
    line-height: 16px;
    color: #00b2ff;
    border: 1px solid #00b2ff; }
    .btn--detail:hover, .btn--detail:focus {
      background-color: #00b2ff;
      color: #ffffff; }
    .btn--detail:active {
      opacity: 0.7; }

.page-header__content {
  background-color: transparent; }

.page-header__title {
  margin: 0;
  margin-bottom: 31px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff; }

.page-header__description {
  margin: 0;
