  font-family: "PT Mono", "Georgia", serif;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  color: #ffffff; }

.page-wrapper {
  background-color: #ffffff;
  background-image: url("../img/bg-adventages-about.png");
  background-position: bottom center;
  background-repeat: no-repeat; }

.advantages__list {
